<script>
export default {
  name: "TopHeader",
  props : ['page_role'],
  data: () => ({}),
  created() {
      this.setRole();
      this.setUserName();
      this.checkUserRole();
  },

  methods : {
      checkUserRole() {
          if(!this.page_role) return;

          const currentRole = this.store('role');
          if(!currentRole) {
              this.$router.push('/auth-page');
          }

          if(currentRole != this.page_role) {
              this.$router.push('/');
          }
      }
  }
};
</script>

